import React from "react";
import http from "../http";
import LoginResponse from "../auth/models/login-response.type";
import { useNavigate } from "react-router-dom";
import ButtonComponent from "../shared-components/buttons/Button.component";
import useToastrStore from "../store/hooks/use-toastr-store.hook";
import { AxiosError } from "axios";
import AppStore from "../store";
import ToastrStore from "../store/slices/toastr-store.slice";
import * as yup from "yup";
import { Formik, Form } from "formik";
import FormikInputFieldComponent from "../shared-components/inputs/FormikInputField.component";

const LoginPage = () => {
  const { addToastr } = useToastrStore();
  const navigate = useNavigate();

  const accessToken = localStorage.getItem("accessToken");
  const initialValues = { username: "kminchelle", password: "0lelplR" };
  const validationSchema = yup.object().shape({
    username: yup.string().required("Username is required"),
    password: yup.string().required("Password is required"),
  });

  const handleLogin = ({ username, password }: { username: string; password: string }) => {
    http
      .post<LoginResponse>("auth/login", { username, password })
      .then((res) => {
        localStorage.setItem("accessToken", res.data.token);
        addToastr({
          variant: "success",
          message: `Welcome ${res.data.firstName} ${res.data.lastName}!`,
        });
        navigate("/");
      })
      .catch((error: AxiosError<{ message: string }>) => {
        AppStore.store.dispatch(ToastrStore.actions.add({ message: error.response?.data.message, variant: "danger" }));
      });
  };

  React.useEffect(() => {
    if (accessToken) {
      navigate("/");
    }
  }, [accessToken, navigate]);

  return (
    <div aria-roledescription="login" className="h-full flex flex-col gap-5 justify-center items-center bg-gray-50">
      <img src="https://img.logoipsum.com/262.svg" alt="Logo" />
      <h1 className="text-3xl font-medium">Sign in to your account</h1>
      <div className="w-full max-w-96 border rounded p-5 shadow-sm bg-white">
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={handleLogin}
          enableReinitialize
        >
          {({ errors, values }) => {
            console.log(errors, values);
            return (
              <Form className="flex flex-col gap-5">
                <FormikInputFieldComponent fieldName="username" required />
                <FormikInputFieldComponent fieldName="password" type="password" required />
                <ButtonComponent text="Login" type="submit" />
              </Form>
            );
          }}
        </Formik>
      </div>
    </div>
  );
};

export default React.memo(LoginPage);
